	import pinglun from "../../components/ping-lun.vue"
    export default {
        data() {
            return {
				howe:{
					id: 0,
					name: "工小界",
					src: "../../static/zx2.jpg",
					title: "变革来啦！包工头、劳务公司即将告别历史舞台？",
					time: "2019/4/15"
				},
				htmlString:
				`法国当地时间2019年4月15日下午6点50分巴黎圣母院发生火灾，
				这座人类著名历史建筑被大火严重破坏。在扼腕叹息之余，“学术
				小镇”通过计算流体力学CFD模拟手段再现了巴黎圣母院的火灾蔓延过程
				，为火灾事故原因调查提供了初步的参考。<br/>
				1. 火灾蔓延CFD模拟<br/>巴黎圣母院主体为砌体拱结构，上部覆盖木结构屋顶，因此，本次大火的主要蔓延区域是木屋顶部分，如下图深红色区域。`,
				dianzan: 0,
				shoucang: 0,
				pinglun: 0,
				dz: 0,
				sc: 0,
				datas:[
					{
						name: "酷酷的天霸",
						img: "../../static/gj.jpg",
						time1: "2019/4/19",
						time2: "21:59",
						cont: "可以，小编写的非常好，文章中生动形象的描述了一段发生火灾时，人们该如何逃跑，而不是灭火！"
					},
					{
						name: "小编二号",
						img: "../../static/jks.jpg",
						time1: "2019/4/18",
						time2: "17:30",
						cont: "惊天还原法国巴黎惨案，巴黎圣母院滔天大火，是人类文明史上的一个重大损失"
					},
					{
						name: "瓦工甲",
						img: "../../static/gr.jpg",
						time1: "2019/4/17",
						time2: "12:28",
						cont: "赞我，我要上一楼！"
					},
					{
						name: "木工乙",
						img: "../../static/zl.jpg",
						time1: "2019/4/16",
						time2: "9:13",
						cont: "我要找工作，谁能翻一下牌。"
					}
				],
				fabpl:{
					name: this.$store.state.nicheng.oname,
					img: this.$store.state.ziliao.osrc,
					time1: "2019/4/19",
					time2: "9:02",
					cont: ""
				}
            }
        },
        onLoad(e) {
			this.howe.name = e.name;
			this.howe.src = e.src;
			this.howe.title = e.title;
			this.howe.time = e.time;
			this.pinglun = this.datas.length;
        },
		methods:{
			pingl(e){
				this.fabpl.cont = e;
				this.datas.push(this.fabpl);
			},
			pltext(e){
				this.fabpl.cont = e.target.value;
			},
			ofenxiang(){
				uni.showModal({
					title: '提示',
					content: '分享功能还未申请！',
					showCancel: false
				});
			},
			fabBtn(){
				if(this.fabpl.cont == ""){
					uni.showModal({
						title: '提示',
						content: '评论不能为空！',
						showCancel: false
					});
				}else{
					this.datas.push(this.fabpl);
					uni.showToast({
						title: '发表成功！',
						duration: 2000
					});
				}
			},
			
			odianzan(){
				if(this.dz == 0){
					this.dz = 1
					this.dianzan ++
				}else{
					this.dz = 0
					this.dianzan --
				}
			},
			oshoucang(){
				if(this.sc == 0){
					this.sc = 1
					this.shoucang ++
				}else{
					this.sc = 0
					this.shoucang --
				}
			}
		},
		components:{
			pinglun
		}
    }
